.App{
  height:100vh;
  width:100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  padding-top: 50px;
  background-color: #6d99e0;
  font-family: 'Boogaloo', cursive;
  margin-top:-30px;
  position:relative;
}

h2{
  font-size:50px;
  margin:0px;
  padding:0px;
}


h4{
  font-size:40px;
  margin-top:20px;
}


img{
  height:180px;
}

.border-black{
  border:3px solid yellow;
}

.border-red{
  border:3px solid red;
  border-radius: 10px;
}

.choices{
  background-color:white;
  padding:20px;
  border-radius: 10px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width:650px;
}

.score{
  font-size: 30px;
}
.result{
  background-color:white;
  margin-top:30px;
  border-radius: 10px;
  padding:16px;
  width:380px;
  height:180px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}


.box-alert-block{
  border-radius: 10px;
  position:absolute;
  height:500px;
  width:800px;
  background-color: #2fed48;
  margin-top:100px;
  display:-ms-flexbox;
  display:flex;
  font-size: 30px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.box-aler-block p{
}

.alert-hidden {
  display:none;
}

.hidden{
  display:none;
}

.block{
  display:block;
}

button{
  padding:10px;
  font-family: 'Boogaloo', cursive;
  background-color: pink;
  border:none;
  font-size: 40px;
  border-radius: 10px;
  -webkit-box-shadow: 0 5px #f4020a;
          box-shadow: 0 5px #f4020a;
  background: -webkit-gradient(linear, left bottom, left top, from(pink), to(yellow));
  background: -webkit-linear-gradient(bottom, pink, yellow);
  background: -o-linear-gradient(bottom, pink, yellow);
  background: linear-gradient(to top, pink, yellow);
}

button:hover{
  background-color: red;
  background: -webkit-gradient(linear, left top, left bottom, from(pink), to(yellow));
  background: -webkit-linear-gradient(top, pink, yellow);
  background: -o-linear-gradient(top, pink, yellow);
  background: linear-gradient(to bottom, pink, yellow);
}
button:active{
  -webkit-box-shadow: 0 2px #f4020a;
          box-shadow: 0 2px #f4020a;
 -webkit-transform: translateY(4px);
     -ms-transform: translateY(4px);
         transform: translateY(4px);
}

.button-hidden{
  display:none;
}

.button-visible{
  border:1px solid white;
}



